body {
  letter-spacing: .03em;
  font-family: 'Poppins', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.poppins {
  font-family: 'Poppins', sans-serif;
}

.icon {
  font-family: 'Material Icons';
}

input {
  letter-spacing: 0.03em;
}

textarea:focus, input:focus{
  outline: none;
}


::-webkit-scrollbar {
  width: 0px;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

ul {
  position: relative;
}

.bg-transition {
  transition: background-color 0.25s ease;
}
.color-transition {
  transition: color 0.25s ease;
}


.fs-purple {
  color: #281D34;
}
.fs-hover-purple:hover {
  color: #281D34;
}
.fs-red {
  color: #F36369;
}
.b-red {
  border-color: #F36369;
}
.fs-hover-red:hover {
  color: #F36369;
}
.fs-bg-hover-red:hover {
  background-color: #F36369;
}
.fs-hover-white:hover {
  color: white;
}
.fs-hover-black:hover {
  color: black;
}
.fs-bg-red {
  background-color: #F36369;
}
.fs-peach {
  color: #FFF2ED;
}
.fs-hover-peach:hover {
  color: #FFF2ED;
}
.fs-bg-dark-gray {
  background-color: #1F1F1F;
}
.fs-bg-light-gray {
  background-color: #F2F2F2;
}
.fs-b-input-light {
  border: 2px solid black;
}
.fs-b-input-dark {
  border: 2px solid white;
}
.fs-bg-light-input-gray {
  background-color: #dddddd;
}
.fs-bg-dark-input-gray {
  background-color: #333333;
}
.fs-b-dark-gray {
  border-color: #1F1F1F;
}
.fs-b-light-gray {
  border-color: #F2F2F2;
}

.fs-bg-purple {
  background-color: #281D34;
}
.fs-light-purple {
  color: #3A284D;
}
.fs-bg-light-purple {
  background-color: #3A284D;
}
.fs-lightest-purple {
  color: #442566;
}
.fs-bg-lightest-purple {
  background-color: #5C4475;
}
.fs-bg-peach {
  background-color: #FFF2ED;
}
.fs-bg-light-peach {
  background-color: #FFF7F3;
}
.fs-bg-lightest-peach {
  background-color: #FFFDFC;
}
.fs-peach-shadow {
  transition: box-shadow .15s ease-in;
  box-shadow: 0px 3px 10px rgba(108, 64, 19, 0.1);
}
.fs-peach-shadow:hover {
  box-shadow: 0px 3px 10px rgba(108, 64, 19, 0.15);
}
.fs-purple-shadow {
  transition: box-shadow .15s ease-in;
  box-shadow: 0px 3px 10px rgba(42, 26, 61, 0.25);
}
.fs-purple-shadow:hover {
  box-shadow: 0px 3px 10px rgba(42, 26, 61, 0.40);
}
.fs-bg-dark-purple {
  background-color: #5C4475;
}
.b-dark-purple {
  border-color: #281D34;
}
.b-light-purple {
  border-color: #3A284D;
}
.b-dark-peach {
  border-color: #FFDACC;
}
.fs-bg-dark-peach {
  background-color: #FFDACC;
}
.fs-b-dark-peach {
  border-color: #FFDACC;
}
.fs-b-dark-purple {
  border-color: #281D34;
}


.fs-dark-purple {
  color: #281D34;
}
.fs-dark-peach {
  color: #FFDACC;
}
.fs-hover-bg-dark-purple:hover {
  background-color: #3A284D;
}
.fs-hover-bg-dark-peach:hover {
  background-color: #FFDACC;
}

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.logo {
  line-height: 80%;
}

.semi-glow {
  transition: opacity .15s ease-in;
}
.semi-glow:hover,
.semi-glow:focus {
  opacity: 0.4;
  transition: opacity .15s ease-in;
}
.hover-unstrike:hover {
  text-decoration: none;
}

.todo-action {
  opacity: 0;
  @extend .fs-dark-purple;

  &.night {
    @extend .fs-dark-peach;
  }
}

li:hover .todo-action {
  opacity: 1;
  transition: opacity .10s ease-in;
}

.todo-action:hover {
  background-color: white;
  @extend .fs-purple;

  &.night {
    background-color: black;
    @extend .fs-peach;
  }
}

.todo-action.primary {
  background-color: white;
  @extend .fs-red;
  opacity: 1;
  &.night {
    background-color: black;
    @extend .fs-red;
  }

  &.disabled {
    opacity: 0.2;
  }
}

.todo-action.primary:hover, .todo-action.primary.night:hover {
  @extend .fs-bg-red;
  @extend .fs-peach;
}

.line-top {
  @extend .fs-b-light-gray;
  border-top-width: 2px;
  border-top-style: solid;
}
.night .line-top {
  @extend .fs-b-dark-gray;
}

.line-border {
  @extend .fs-b-light-gray;
  border-width: 2px;
  border-style: solid;
}
.night .line-border {
  @extend .fs-b-dark-gray;
}

.hamburger {
  path {
    stroke: #000000;
  }
  &.night {
    path {
      stroke: #ffffff;
    }
  }
}

.highlight-background {
  @extend .fs-bg-light-peach;
  @extend .fs-peach-shadow;
}
.night .highlight-background {
  @extend .fs-bg-light-purple;
  @extend .fs-purple-shadow;
}

.mvh-50 {
  max-height: 50vh;
}

.cursor-text:hover {
  cursor: text;
}
